import { Component, OnInit } from "@angular/core";
import {
  trigger,
  state,
  style,
  animate,
  transition,
} from "@angular/animations";

@Component({
  selector: "app-faq-section",
  templateUrl: "./faq-section.component.html",
  styleUrls: ["./faq-section.component.scss"],
  animations: [
    trigger("expandCollapse", [
      state(
        "collapsed",
        style({
          height: "0",
          opacity: "0",
          overflow: "hidden",
        })
      ),
      state(
        "expanded",
        style({
          height: "*",
          opacity: "1",
          overflow: "visible",
        })
      ),
      transition("collapsed <=> expanded", [animate("300ms ease-in-out")]),
    ]),
    trigger("rotateAnimation", [
      state(
        "rotated",
        style({
          transform: "rotate(45deg)",
        })
      ),
      state(
        "notRotated",
        style({
          transform: "rotate(0deg)",
        })
      ),
      transition("notRotated <=> rotated", [animate("0.3s ease-in-out")]),
    ]),
  ],
})
export class FaqSectionComponent implements OnInit {
  isExpanded = -1;
  public questions: { title: String; body: String }[] = [
    {
      title: "Hur anvnds Templett-Stickers?",
      body: "Templett-Stickers anvnds genom att butiksgare kper stickers med insatta NFC-chip frn Templett. Dessa stickers placeras sedan i butiken och kopplas till en specifik webbadress eller hemsida. Nr kunderna passerar med en smartphone, kan de enkelt 'blippa' sin telefon mot dessa stickers fr att n information eller lnkar till olika onlineplatser eller tjnster.",
    },
    {
      title: "Kan min verksamhet dra nytta av Templett Stickers?",
      body: "Absolut! Templett stickers gr att anvnda fr alla verksamheter som har tillgng till en hemsida med information som de enklare vill frmedla till kunderna. Ju mer information som finns p hemsidan desto fler mjligheter finns det att ka kundengagemang eller frsljning med hjlp av Templett-stickers.",
    },
    {
      title: "Vilka stickers behver jag?",
      body: "Fr att anvnda Templett-Stickers behver du stickers som r utrustade med de programmerade chip som Templett tillhandahller. Dessa chippade stickers kan sedan ndras fr att leda till nskade webbadresser eller hemsidor nr de blippas med via smarta mobila enheter. Utver dessa chippade stickers krvs ven Templett's modul-stickers som informerar kunden om vart de hamnar d de blippar en sticker.",
    },
    {
      title: "Vart stter jag chippen?",
      body: "Chippen kan placeras p olika platser inom din butik, exempelvis p insidan av kundkorgar, handtaget p kundvagnar eller andra ytor som r tillgngliga fr kunder. Det r viktigt att placera dem p platser dr kunder ltt kan interagera med dem med sina mobila enheter fr att f tkomst till den nskade informationen eller webbplatsen.",
    },
    {
      title: "Vad kan chippen gra?",
      body: "Templett-chippen gr det mjligt fr dina kunder att blippa sina smartphones mot dina Templett-Stickers. Genom att interagera med chippen i Templett-stickers kan kunderna f tillgng till webbadresser, hemsidor eller annan information som r programmerad till chippen. Det kan vara produktinformation, kampanjer, webbplatser eller annat innehll relaterat till din butik eller dina tjnster.",
    },
    {
      title: "Kan jag ndra vad chippen leder till?",
      body: "Ja, du kan enkelt ndra vilken webbadress eller hemsida Templett-chippen leder till. Kontakta Templett via kontakt@templett.se fr att uppdatera destinationswebbadressen som r kopplad till chippen i dina Templett-stickers. Detta gr det mjligt att anpassa och uppdatera informationen som kunderna nr nr de interagerar med dina Templett-stickers.",
    },
  ];

  toggleExpand(index: number) {
    this.isExpanded = index;
  }

  ngOnInit() {}
}
