import { Component, OnInit } from "@angular/core";
import {
  trigger,
  state,
  style,
  animate,
  transition,
} from "@angular/animations";
@Component({
  selector: "app-manual-downloads",
  templateUrl: "./manual-downloads.component.html",
  styleUrls: ["./manual-downloads.component.scss"],
  animations: [
    trigger("expandCollapse", [
      state(
        "collapsed",
        style({
          height: "0",
          opacity: "0",
          overflow: "hidden",
        })
      ),
      state(
        "expanded",
        style({
          height: "*",
          opacity: "1",
          overflow: "visible",
        })
      ),
      transition("collapsed <=> expanded", [animate("300ms ease-in-out")]),
    ]),
    trigger("rotateAnimation", [
      state(
        "rotated",
        style({
          transform: "rotate(45deg)",
        })
      ),
      state(
        "notRotated",
        style({
          transform: "rotate(0deg)",
        })
      ),
      transition("notRotated <=> rotated", [animate("0.3s ease-in-out")]),
    ]),
  ],
})
export class ManualDownloadsComponent {
  isExpanded = -1;
  public questions: { title: String; body: String; href?: String }[] = [
    {
      title: "Templett Modulra NFC-Stickers - Matbutiker",
      body: "Templett_NFC_Stickers_Anvndarmanual_svenska.pdf ",
      href: "https://drive.google.com/uc?export=download&id=186NegHRc_Y4Vayvke9P9NLkicdiwFEui",
    },
    {
      title: "Templett Modulra NFC-Stickers - Gym",
      body: "Kommer snart.",
    },
    {
      title: "Templett Modulra NFC-Stickers - Resturanger",
      body: "Kommer snart.",
    },
  ];

  toggleExpand(index: number) {
    this.isExpanded = index;
  }
}
